@import url(https://fonts.googleapis.com/css?family=Catamaran|Libre+Franklin|PT+Sans+Narrow|Source+Serif+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-image: -webkit-linear-gradient(top, #040404, #040404, #040404, #1e090f);
  background-image: linear-gradient(to bottom, #040404, #040404, #040404, #1e090f);
  width: 100%;
  height: 100vh !important;
  font-family: 'Source Serif Pro', serif; }

#calc {
  position: absolute;
  top: 10%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 40vmax;
  height: 80vh;
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid #2e0e17;
  border-radius: 5px;
  box-shadow: 0px 0px 50px 7px black; }

#display {
  position: absolute;
  top: 3%;
  left: 6%;
  right: 6%;
  height: 25%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 1px 3px 4px #0d0d0d inset;
  border: 1px solid #260b13;
  border-width: 0px 1px 1px 0px;
  overflow: hidden;
  cursor: default;
  align-items: flex-end; }

#input {
  font-size: 1.8em;
  color: white;
  padding: 2%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

#exp {
  font-size: 1.8em;
  color: #404040;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

#keypad {
  position: absolute;
  top: 33%;
  left: 6%;
  right: 6%;
  bottom: 2%;
  height: 62%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 7px; }
  #keypad .key {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    color: white;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 1.8em;
    background: rgba(0, 0, 0, 0.9);
    border-radius: 4%;
    text-align: center;
    vertical-align: middle !important;
    border: 1px solid #260b13;
    border-top: 0px;
    box-shadow: 1px 3px  4px black; }
  #keypad #key-AC {
    grid-column: 1 / 3;
    background: -webkit-repeating-linear-gradient(45deg, #000000, #4d1300 3%);
    background: repeating-linear-gradient(45deg, #000000, #4d1300 3%); }
  #keypad #key-0 {
    grid-column: 1 / 3; }
  #keypad #key-eq {
    grid-row: 4 / 6;
    grid-column: 4 / 5;
    background: -webkit-linear-gradient(45deg, #1e090f, #000000, #1e090f, #000000, #1e090f, #000000);
    background: linear-gradient(45deg, #1e090f, #000000, #1e090f, #000000, #1e090f, #000000); }

.anim {
  border-top: 2px groove #e68a00 !important;
  box-shadow: 1px 3px  4px #663d00 !important; }

@media only screen and (max-width: 800px) {
  body #calc {
    top: 5%;
    width: 96%;
    height: 88vh;
    border-color: #55192a; }
  body #display {
    border-color: #3d121f; }
  body #keypad {
    grid-gap: 5px; }
    body #keypad .key {
      border-color: #3d121f; } }

body {
  overflow: hidden;
  margin: 0 !important;
  padding: 0; }

